export default{
	data(){
		return {
			currentIndex:0,
		}
	},
	props:{
		className:{
			type:String,
			required:false,
		},
		banners:{
			type:Array,
			default:()=>{
				return []
			},
			required:true
		},
		options:{
			type:Object,
			required:false,
			default:()=>{
				return {
					indicatorDots:false,
					autoplay:true,
				}
			}
		}
	},
	methods:{
		swiperChange(e){
			this.currentIndex = e.detail.current
			this.$emit('change',this.currentIndex)
		},
		tapItem(i){
			this.$emit('click',i)
		}
	}
}
